.App {
  /* text-align: center; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.navbar{
  border-bottom: 2px solid;
}

.navbar ul{
  list-style: none;
}

.navbar ul li{
  display: inline;
  border: 1px dotted;
  margin: 3px;
}

.search{
  margin:10px;
}

.listing,.favorite-list {
      display: flex;
    flex-flow: wrap;
    justify-content: space-around;
}